


































import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import accountSelect from '@/components/common-components/accountSelect'
import wx from 'weixin-js-sdk'
import { formatDate } from '@/until/index'

@Component({
  components: {
    accountSelect
  },
  filters: {
    getFormatDate (date: any) {
      if (date) {
        return new Date(date).getFullYear() + '年' + (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '号'
      } else {
        return ''
      }
    },
    getImage (item: any) {
      if (item.commodity_info[0] && item.commodity_info[0].imgUrlList) {
        return item.commodity_info[0].imgUrlList[0]
      } else {
        return ''
      }
    },
    getType (item: any) {
      if (item.delivery) {
        return '配送成功'
      } else {
        return '配送中'
      }
    }
  }
})
export default class payAdmin extends Vue {
  openid = ''
  page = 1
  orderList = []
  isShowMore = false
  // 计算属性
  get formatMonth () {
    return this.format(this.selectDate, 'yyyy-MM')
  }
  activated () {
    this.pageInit()
  }

  pageInit () {
    let query = this.$route.query
    if (query.openid) {
      this.openid = query.openid
      this.getOrderList()
    } else if (query.code) {
      this.getUserInfo()
    }
  }

  getOrderList () {
    const toast = this['$createToast']({
      time: 0,
      txt: '数据获取中...'
    })
    toast.show()
    this.$store.dispatch('getOrderList', { openid: this.openid, page: this.page }).then((response) => {
      if (response.success) {
        let resData = response.data
        if (response.data.list.length >= 0 && response.data.list.length < 10) {
          this.orderList = this.orderList.concat(resData.list)
          this.isShowMore = false
        } else if (response.data.list.length === 10){
          this.orderList = this.orderList.concat(resData.list)
          this.isShowMore = true
        } else {
          this.isShowMore = false
        }
      }
      toast.hide()
    })
  }

  getUserInfo () {
    let query = this.$route.query
    if (query.code) {
      const toast = this['$createToast']({
        time: 0,
        txt: '数据获取中...'
      })
      toast.show()
      this.$store.dispatch('getCommodityUserInfo', { code: query.code }).then((response) => {
        if (response.success) {
          if (response.data.openid) {
            this.openid = response.data.openid
            this.getOrderList()
            if (response.data.userInfo) {
              this.userInfo = response.data.userInfo
            }
            // this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } })
          }
        }
        toast.hide()
      })
    } else {
      console.log('不是微信登陆')
    }
  }

  showMoreClick () {
    this.page++
    this.getOrderList()
  }

  format (date, format) {
    //eg:format="yyyy-MM-dd hh:mm:ss";

    if (!format) {
      format = 'yyyy-MM-dd hh:mm:ss'
    }
    var o = {
      'M+': date.getMonth() + 1,  // month
      'd+': date.getDate(),       // day
      'H+': date.getHours(),      // hour
      'h+': date.getHours(),      // hour
      'm+': date.getMinutes(),    // minute
      's+': date.getSeconds(),    // second
      'q+': Math.floor((date.getMonth() + 3) / 3), // quarter
      'S': date.getMilliseconds()
    }

    if (/(y+)/.test(format)) {
      format = format.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
    }

    for (var k in o) {
      if (new RegExp('(' + k + ')').test(format)) {
        format = format.replace(RegExp.$1, RegExp.$1.length === 1
          ? o[k]
          : ('00' + o[k]).substr(('' + o[k]).length))
      }
    }
    return format
  }
}
